:export {
  --primary-color-lightest: #bfdafa;
  --primary-color-light: #a5bce0;
  --primary-color: #4276c9;
  --primary-color-dark: #305ea8;
  --white: #ffffff;
  --grey-light: #f3f6f9;
  --grey: #a5a5a5;
  --grey-dark: #2e2e2e;
  --black: #000000;
}

:global(.text-primary) {
	color: #4276c9;
}

:global(.text-grey) {
	color: #a5a5a5;
}


