:import("../../style_vars/colors.css"){
  i__const___primary_color_11: --primary-color;
  i__const___primary_color_light_12: --primary-color-light;
  i__const___primary_color_dark_13: --primary-color-dark;
  i__const___grey_light_14: --grey-light;
  i__const___grey_15: --grey;
  i__const___grey_dark_16: --grey-dark;
}
:import("../../style_vars/typography.css"){
  i__const___primary_font_17: --primary-font;
}
:export{
  colors: "../../style_vars/colors.css";
  --primary-color: i__const___primary_color_11;
  --primary-color-light: i__const___primary_color_light_12;
  --primary-color-dark: i__const___primary_color_dark_13;
  --grey-light: i__const___grey_light_14;
  --grey: i__const___grey_15;
  --grey-dark: i__const___grey_dark_16;
  typography: "../../style_vars/typography.css";
  --primary-font: i__const___primary_font_17;
}
@import '../../style_vars/colors.css';
@import '../../style_vars/typography.css';

/* import paths for global variables */

/* base style for all buttons */
.button {
	border-radius: 3px;
	text-align: center;
	font-family: i__const___primary_font_17;
	cursor: pointer;
	position: relative;
	text-decoration: none;
}

.button:hover {
	text-decoration: none;
}

/* default/secondary button style */
.normal {
	composes: button;
	color: i__const___grey_dark_16;
	border: solid 1px i__const___grey_dark_16;
	background-color: #fff;
	text-decoration: none;
}

.normal:hover {
	color: i__const___primary_color_11;
	border: solid 1px i__const___primary_color_11;
}

.normal.disabled, 
.normal-disabled:hover {
	color: i__const___grey_15;
	border: solid 1px i__const___grey_15;
	cursor: not-allowed;
}

/* colored primary style */
.primary {
	composes: button;
	color: #ffffff;
	border: solid 1px i__const___primary_color_11;
	background-color: i__const___primary_color_11;
	font-weight: bold;
	text-decoration: none;
}

.primary:hover {
	background-color: i__const___primary_color_dark_13;
	border-color: i__const___primary_color_dark_13;
	color: #fff;
}

.primary.disabled, 
.primary-disabled:hover {
	border-color: i__const___primary_color_light_12;
	background-color: i__const___primary_color_light_12;
	cursor: not-allowed;
}

/* default button style when on dark background */
.inverse {
	composes: button;
	color: #ffffff;
	border: solid 1px #ffffff;
	background-color: transparent;
	text-decoration: none;
}

.inverse:hover {
	color: i__const___grey_dark_16;
	border: solid 1px i__const___grey_light_14;
	background-color: i__const___grey_light_14;
}

.inverse.disabled, 
.inverse.disabled:hover {
	color: i__const___grey_15;
	border: solid 1px i__const___grey_15;
	background-color: transparent;
	cursor: not-allowed;
}

/* sizing */
.normalSize {
	font-size: 12px;
	padding: 7px 20px;
	line-height: 16px;
}

.largeSize {
	font-size: 16px;
	padding: 12px 20px;
	line-height: 22px;
}

/* full width */
.block {
	display: block;
	width: 100%;
}

/* style button to look like text link */
.textLink {
	composes: button;
	color: i__const___primary_color_11;
	background-color: transparent;
	border: none;
	text-decoration: none;
	font-size: 14px;
}

.textLink:hover {
	text-decoration: underline;
	color: i__const___primary_color_dark_13;
}

.textLink.disabled {
	color: i__const___grey_15;
	cursor: not-allowed;
}

.textLink.disabled:hover {
	text-decoration: none;
}

/* ==== loading styles ==== */
.loading {
	composes: button;
	color: transparent;
	pointer-events: none;
}

/* create the spinner icon base properties */
.loading:after {
	left: 50%;
	position: absolute;
	-webkit-animation: spin 500ms infinite linear;
	        animation: spin 500ms infinite linear;
	border-radius: 290486px;
	content: "";
	display: block;
}

@-webkit-keyframes spin {
	0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
	0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* spinner icon size props based on button size */
.normalSize.loading:after {
	height: 12px;
	width: 12px;
	margin-left: -6px;
	margin-right: 6px;
	top: 6px;
}

.largeSize.loading:after {
	height: 16px;
	width: 16px;
	margin-left: -8px;
	margin-right: 8px;
	top: 12px;
}

/* create spinner icon display based on button color */
.normal.loading:after {
	border: 2px solid;
	border-color: transparent transparent i__const___grey_15 i__const___grey_15;
}

.primary.loading:after, 
.inverse.loading:after {
	border: 2px solid;
	border-color: transparent transparent #fff #fff;
}
/* ==== /loading styles ==== */



